import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useParams, useNavigate } from 'react-router-dom';
import { UploadDiv, UploadButtonDiv, UploadForm } from '../../Style/UploadCSS';
import ImageUpload from './ImageUpload';

//  :     
function Edit() {
  const params = useParams();
  const navigate = useNavigate();
  const [postInfo, setPostInfo] = useState({});
  const [title, setTitle] = useState('');
  const [content, setContent] = useState('');
  const [image, setImage] = useState(`http://localhost:4000/${postInfo.image}`);
  useEffect(() => {
    let body = {
      postNum: params.postNum,
    };
    axios
      .post('/api/post/detail', body)
      .then(res => {
        if (res.data.success) {
          setPostInfo(res.data.postDetail);
        } else {
          alert('   ');
        }
      })
      .catch(err => {
        console.log(err);
      });
  }, [params.postNum]);
  const onSubmit = e => {
    e.preventDefault();
    if (title === '' || content === '') {
      alert('    !');
    }
    let body = {
      title: title,
      content: content,
      postNum: params.postNum,
      image: image,
    };
    axios
      .post('/api/post/edit', body)
      .then(res => {
        if (res.data.success) {
          alert('   ');
          navigate(`/post/${params.postNum}`);
        } else {
          alert('   ');
        }
      })
      .catch(err => {
        alert(`     .:${err}`);
      });
  };

  useEffect(() => {
    setTitle(postInfo.title);
    setContent(postInfo.content);
    console.log(postInfo.image);
    setImage(postInfo.image);
  }, [postInfo]);

  return (
    <UploadDiv>
      <UploadForm>
        <label htmlFor="title">  </label>
        <input
          id="title"
          placeholder="   "
          value={title}
          onChange={e => {
            setTitle(e.target.value);
          }}
        />
        <label htmlFor="content">  </label>

        <ImageUpload onChange={e => setImage(e)} />
        <textarea
          placeholder="   "
          id="content"
          value={content}
          onChange={e => {
            setContent(e.target.value);
          }}
        />
        <UploadButtonDiv>
          <button
            className="cancel"
            onClick={e => {
              e.preventDefault();
              navigate(-1);
            }}
          >
            
          </button>
          <button
            onClick={e => {
              onSubmit(e);
            }}
          >
            
          </button>
        </UploadButtonDiv>
      </UploadForm>
    </UploadDiv>
  );
}

export default Edit;
