import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useParams, Link, useNavigate } from 'react-router-dom';
import Spinner from 'react-bootstrap/Spinner';
import { PostBox, BtnDiv, PostDiv } from '../../Style/DetailListCSS';

function Detail() {
  const params = useParams();
  const navigate = useNavigate();
  const [postInfo, setPostInfo] = useState({});
  const [flag, setFlag] = useState(false);
  useEffect(() => {
    let body = {
      postNum: params.postNum,
    };
    axios
      .post('/api/post/detail', body)
      .then(res => {
        if (res.data.success) {
          setPostInfo(res.data.postDetail);
          setFlag(true);
        } else {
          alert('   ');
        }
      })
      .catch(err => {
        console.log(err);
      });
  }, [params.postNum]);

  useEffect(() => {
    console.log(postInfo);
  }, [postInfo]);

  const DeleteHandler = () => {
    if (window.confirm('    ?')) {
      let body = {
        postNum: params.postNum,
      };
      axios
        .post('/api/post/delete', body)
        .then(res => {
          if (res.data.success) {
            setPostInfo(res.data.postDetail);
            alert(' ');
            navigate('/');
          }
        })
        .catch(err => {
          alert('     ');
        });
    } else {
      alert(' ');
    }
  };
  return (
    <PostDiv>
      {flag ? (
        <>
          <PostBox>
            <div className="title">{postInfo.title} </div>
            <div>
              {postInfo.image ? (
                <img
                  style={{ width: '100%', height: 'auto' }}
                  src={`http://localhost:4000/${postInfo.image}`}
                  alt={postInfo.image}
                />
              ) : null}
            </div>
            <div className="content">{postInfo.content}</div>
          </PostBox>
          <BtnDiv>
            <Link to={`/edit/${postInfo.postNum}`}>
              <button className="edit"></button>
            </Link>
            <Link to="">
              {' '}
              <button
                className="delete"
                onClick={() => {
                  DeleteHandler();
                }}
              >
                
              </button>
            </Link>
          </BtnDiv>
        </>
      ) : (
        <Spinner animation="border" role="status">
          <span className="visually-hidden">Loading...</span>
        </Spinner>
      )}
    </PostDiv>
  );
}

export default Detail;
